import React, { useRef, useState } from 'react'
import emailjs from '@emailjs/browser'

const Contact = () => {
  const [done, setDone] = useState(false)
  const form = useRef()

  const sendEmail = (e) => {
    e.preventDefault()

    emailjs
      .sendForm(
        'service_sk7gaxm',
        'template_da5z61n',
        form.current,
        'CraAtqs_tSGi8ps7R'
      )
      .then(
        (result) => {
          console.log(result)
          setDone(true)
          form.current.reset()
        },
        (error) => {
          console.log(error.text)
        }
      )
  }

  return (
    <section className='text-gray-600 body-font relative'>
      <div className='container px-5 pt-14 mx-auto'>
        <div className='flex flex-col text-center w-full mb-12'>
          <h1 className='sm:text-3xl text-2xl font-medium title-font mb-4 text-gray-900'>
            Contact Me
          </h1>
          <p className='lg:w-2/3 mx-auto leading-relaxed text-base'>
            For any queries or conversasion feel free to use this form to
            contact me.
          </p>
        </div>
        <div className='lg:w-1/2 md:w-2/3 mx-auto'>
          <form className='flex flex-wrap -m-2' ref={form} onSubmit={sendEmail}>
            <div className='p-2 w-1/2'>
              <div className='relative'>
                <label className='leading-7 text-sm text-gray-600'>Name</label>
                <input
                  type='text'
                  name='user_name'
                  className='w-full bg-gray-100 bg-opacity-50 rounded border border-gray-300 focus:border-green-500 focus:bg-white focus:ring-2 focus:ring-green-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out'
                />
              </div>
            </div>
            <div className='p-2 w-1/2'>
              <div className='relative'>
                <label className='leading-7 text-sm text-gray-600'>Email</label>
                <input
                  type='email'
                  name='user_email'
                  className='w-full bg-gray-100 bg-opacity-50 rounded border border-gray-300 focus:border-green-500 focus:bg-white focus:ring-2 focus:ring-green-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out'
                />
              </div>
            </div>
            <div className='p-2 w-full'>
              <div className='relative'>
                <label className='leading-7 text-sm text-gray-600'>
                  Message
                </label>
                <textarea
                  name='message'
                  className='w-full bg-gray-100 bg-opacity-50 rounded border border-gray-300 focus:border-green-500 focus:bg-white focus:ring-2 focus:ring-green-200 h-32 text-base outline-none text-gray-700 py-1 px-3 resize-none leading-6 transition-colors duration-200 ease-in-out'
                ></textarea>
              </div>
            </div>
            <div className='p-2 w-full'>
              <button
                type='submit'
                value='Send'
                className='flex mx-auto text-white bg-green-500 border-0 py-2 px-8 focus:outline-none hover:bg-green-600 rounded text-lg'
              >
                Send
              </button>
            </div>
          </form>
          {done && (
            <p className='lg:w-2/3 mx-auto leading-relaxed text-base text-green-600 uppercase pt-2 text-center'>
              message sent successfully. thank you.
            </p>
          )}
        </div>
      </div>
    </section>
  )
}

export default Contact
