import React, { useState, useEffect } from 'react'
import sanityClient from '../client'
import { PortableText } from '@portabletext/react'

const About = () => {
  const [data, setData] = useState(null)
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    sanityClient
      .fetch(
        `*[_type=='about']{
      title1,
      title2,
      mainImage{
        asset->{
          _id,url
        },
        alt
      },
      body,
      linkedInUrl,
      githubUrl
    }`
      )
      .then((newData) => {
        setData(newData[0])
        setLoading(false)
      })
      .catch((error) => {
        console.error('Error : ', error)
      })
  }, [])

  return loading ? (
    <p className='flex items-center justify-center capitalize pt-14'>loading</p>
  ) : (
    <section className='text-gray-600 body-font'>
      <div className='h-auto sm:h-[80vh] w-4/5 mx-auto  flex flex-col justify-center items-start pt-14 sm:pt-0'>
        <h1 className='title-font sm:text-4xl text-3xl mb-4 font-medium text-gray-900 capitalize'>
          {data.title1 + ' , '}
          <br className='inline-block' />
          {data.title2}
        </h1>

        <div className='pt-4 mb-8 leading-relaxed capitalize'>
          <PortableText value={data.body} />
        </div>

        <div className='flex justify-center'>
          <a
            href={data.linkedInUrl}
            target='_blank'
            rel='noreferrer'
            className='inline-flex text-white bg-green-500 border-0 py-2 px-6 focus:outline-none hover:bg-green-600 rounded text-lg'
          >
            LinkedIn
          </a>
          <a
            href={data.githubUrl}
            target='_blank'
            rel='noreferrer'
            className='ml-4 inline-flex text-gray-700 bg-gray-100 border-0 py-2 px-6 focus:outline-none hover:bg-gray-200 rounded text-lg'
          >
            Github
          </a>
        </div>
      </div>
    </section>
  )
}

export default About
