import React, { useState, useEffect } from 'react'
import sanityClient from '../client'
import { PortableText } from '@portabletext/react'

const Projects = () => {
  const [data, setData] = useState(null)
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    sanityClient
      .fetch(
        `*[_type=='projects']{
      title,
      slug,
      mainImage{
        asset->{
          _id,url
        },
        alt
      },
      publishedAt,
      body,
      liveUrl,
      githubUrl
    }`
      )
      .then((newData) => {
        setData(newData)
        setLoading(false)
      })
  }, [])

  return loading ? (
    <p className='flex items-center justify-center capitalize pt-14'>loading</p>
  ) : (
    <section className='text-gray-600 body-font'>
      <div className='container px-5 pt-14 mx-auto'>
        <div className='flex flex-col text-center w-full mb-20'>
          <h1 className='sm:text-3xl text-2xl font-medium title-font mb-4 text-gray-900'>
            My Projects
          </h1>
          <p className='lg:w-2/3 mx-auto leading-relaxed text-base'>
            Here are some of the best projects I've done. You can see the live
            site. You can also find the code behind those projects by clicking
            the 'Github Link'.
          </p>
        </div>
        {data &&
          data.map((singleproject, index) => (
            <div key={index} className='flex flex-wrap -m-4'>
              <div className='lg:w-1/3  sm:w-1/2 w-full p-4'>
                <div className='flex relative'>
                  <img
                    alt={singleproject.mainImage.alt}
                    className='absolute inset-0 w-full h-full object-cover object-center'
                    src={singleproject.mainImage.asset.url}
                  />
                  <div className='px-8 py-10 relative z-10 w-full border-4 border-gray-200 bg-white opacity-0 hover:opacity-100'>
                    <h1 className='title-font text-2xl font-bold text-gray-900 mb-3 uppercase'>
                      {singleproject.title}
                    </h1>
                    <p className='my-4 capitalize'>
                      Completed on :
                      {new Date(singleproject.publishedAt).toLocaleDateString()}
                    </p>
                    <div className='capitalize'>
                      <PortableText value={singleproject.body[index]} />
                    </div>
                    <div className=' flex flex-col items-baseline space-y-4 sm:space-x-4 mt-4 sm:flex-row'>
                      <a
                        href={singleproject.liveUrl}
                        className='w-36 text-white bg-green-500 border-0 py-2 px-6 focus:outline-none hover:bg-green-600 rounded text-lg'
                        target='_blank'
                        rel='noreferrer'
                      >
                        Live Link
                      </a>
                      <a
                        href={singleproject.githubUrl}
                        className='w-36 text-gray-700 bg-gray-100 border-0 py-2 px-6 focus:outline-none hover:bg-gray-200 rounded text-lg'
                        target='_blank'
                        rel='noreferrer'
                      >
                        Github Link
                      </a>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          ))}
      </div>
    </section>
  )
}

export default Projects
