import React, { useState, useEffect } from 'react'
import { NavLink } from 'react-router-dom'
import Logo from './../logo.png'

const Navbar = () => {
  const [open, setOpen] = useState(false)

  const controllNav = () => {
    if (open && window.scrollY > 0) {
      setOpen((prevState) => !prevState)
      console.log('scrolled')
    }
  }

  const handleClick = () => {
    if (open) {
      setOpen((prevState) => !prevState)
    }
  }

  useEffect(() => {
    window.addEventListener('scroll', controllNav)
    return () => {
      window.removeEventListener('scroll', controllNav)
    }
  })

  const navClass = ({ isActive }) =>
    isActive
      ? 'bg-green-500 text-white text-xl mr-5 hover:text-gray-900 hover:cursor-pointer p-2 rounded '
      : 'text-xl mr-5 hover:text-gray-900 hover:cursor-pointer p-2 rounded '

  return (
    <div className='w-full mx-auto flex py-4 px-8 items-center justify-between shadow relative'>
      <div className='align-middle'>
        <NavLink
          to='/'
          className='flex items-center title-font font-medium text-gray-900 cursor-pointer w-full'
          onClick={handleClick}
        >
          <img className='w-8' src={Logo} alt='logo' />
          <span className='ml-3 text-2xl'>Akram</span>
        </NavLink>
      </div>
      <div
        className='sm:hidden text-3xl  cursor-pointer'
        onClick={() => setOpen(!open)}
      >
        <ion-icon name={open ? 'close' : 'menu'}></ion-icon>
      </div>
      <div
        className={`sm:flex flex-col items-center justify-end sm:flex-row absolute sm:static top-[4.25rem] w-full sm:w-auto bg-slate-300 sm:bg-white py-6 sm:py-0 transition-all ease-in-out duration-500 z-10 sm:z-0 ${
          open ? 'left-0 flex' : 'hidden'
        }`}
      >
        <NavLink to='/' className={navClass} onClick={handleClick}>
          About
        </NavLink>
        <NavLink to='/projects' className={navClass} onClick={handleClick}>
          Projects
        </NavLink>
        <NavLink to='/contact' className={navClass} onClick={handleClick}>
          Contact
        </NavLink>
      </div>
    </div>
  )
}

export default Navbar
